import React, { forwardRef } from "react";

import { Icon } from "../Icon/Icon";

import {
	ButtonText, IconWrapper, StyledButton,
} from "./ButtonGroup.styles";
import {
	ButtonGroupButtonProps,
} from "./ButtonGroup.types";

const ButtonGroupButton = forwardRef<HTMLButtonElement, ButtonGroupButtonProps>(({
	dataQA,
	buttonText,
	ariaLabel,
	buttonSize,
	icon,
	isSelected = false,
	$isDisabled,
	onClick,
	leftIcon,
	rightIcon,
	children,
	className,
	...props
}, ref) => (
	<StyledButton
		type="button"
		disabled={$isDisabled}
		onClick={onClick}
		className={className}
		buttonSize={buttonSize}
		data-qa={dataQA}
		aria-label={ariaLabel}
		isSelected={isSelected}
		ref={ref}
		{...props}
	>
		{leftIcon && (
			<IconWrapper
				data-qa={`${dataQA}-left-icon`}
				aria-label={`${ariaLabel}-button-left-icon`}
			>
				<Icon name={leftIcon} size={20} />
			</IconWrapper>
		)}
		{icon ? (
			<IconWrapper
				data-qa={`${dataQA}-icon`}
				aria-label={`${ariaLabel}-icon`}
			>
				<Icon name={icon} size={20} />
			</IconWrapper>
		) : (
			<ButtonText
				dataQA="button-text"
				variant="bodyCopyBold"
				component="span"
				color="inherit"
				align="center"
				m="auto"
			>
				{buttonText}
				{children}
			</ButtonText>
		)}
		{rightIcon && (
			<IconWrapper
				data-qa={`${dataQA}-right-icon`}
				aria-label={`${ariaLabel}-right-icon`}
			>
				<Icon name={rightIcon} size={20} />
			</IconWrapper>
		)}
	</StyledButton>
));

ButtonGroupButton.displayName = "ButtonGroupButton";
export default ButtonGroupButton;

